export const categories = [
  { id: "emergency", label: "", icon: "", color: "text-red-500" },
  { id: "cleaning", label: "", icon: "", color: "text-blue-500" },
  { id: "lighting", label: "", icon: "", color: "text-yellow-500" },
  { id: "lift", label: "", icon: "", color: "text-purple-500" },
  { id: "heating", label: "", icon: "", color: "text-orange-500" },
  { id: "repair", label: "", icon: "", color: "text-green-500" },
  { id: "parking", label: "", icon: "", color: "text-cyan-500" },
  { id: "playground", label: " ", icon: "", color: "text-pink-500" },
  { id: "noise", label: "", icon: "", color: "text-indigo-500" },
  { id: "other", label: "", icon: "", color: "text-gray-500" },
]

export function getCategoryInfo(categoryId: string) {
  return categories.find((c) => c.id === categoryId) || categories[categories.length - 1]
}
